import React from 'react'
import Fade from '@material-ui/core/Fade';
import isStart from '../ProblemPage/ProblemPage'
import './styles.css'
import ProblemPage from '../ProblemPage/ProblemPage';

export default class QuizPage extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            checked: false,
            isStart: false,
        }
    }
    componentDidMount(){
        var that = this;
		setTimeout(function() {
			that.show();
		}, that.props.wait);
	}
	show() {
        var that = this
		var delay = function(s){
            return new Promise(function(resolve, reject){
                setTimeout(resolve,s); 
            });
        };
        delay().then(function(){
            that.setState({checked: true})
        })
    }
    toggleStart = () => {
        this.setState({ isStart: true })
    }
    render(){
        if (!this.state.isStart){
            return(
                <div>
                    <Fade in={this.state.checked} timeout={2000}>
                        <div className="mycanvas">
                            <div className="text">
                                <h1 style={{color: "white", marginBottom: "5%"}}>遊戲規則：</h1>
                                <div className="rule">
                                    <h2>遊戲中有數道關卡</h2>
                                    <h2>所有關卡都需要同學們來決策下一步該如何進行</h2>
                                    <h2>有的決策會讓遊戲情境中的二氧化碳濃度瞬間提升</h2>
                                    <h2>有的則能延緩二氧化碳濃度的升高速率</h2>
                                </div>
                                <button onClick={this.toggleStart} style={{margin: "5%"}}>了解了！開始！</button>
                            </div>
                        </div>
                    </Fade>
                </div>
            )
        }
        else {
            return(
                <div>
                    <ProblemPage/>
                </div>
            )
        }
    }
}