import React from 'react'
import Fade from '@material-ui/core/Fade';
import problems from '../../components/Problems/Problems'
import AcUnitIcon from '@material-ui/icons/AcUnit';
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import './styles.css'
import * as PIXI from 'pixi.js'

export default class ProblemPage extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            checked: false,
            checked2: false,
            progressValue: 0,
            isGameOver: false,
            isClear: false,
            quizNum: 0,
            app: undefined,
        }
    }
    setupPixi = () => {
        const mycanvas = document.getElementById("progressbar")
        const mycanvasWidth = document.getElementById('progressbar').offsetWidth + 500
        const mycanvasHeight = document.getElementById('progressbar').offsetHeight + 300
        console.log(mycanvasHeight)
        let app = new PIXI.Application({
            view: mycanvas,
            width: window.innerWidth,
            height: window.innerHeight,  
            antialias: true,
            transparent: true,
            resolution: 1
        });
        this.setState({ app: app })
        var graphics = new PIXI.Graphics();
        let value = 0
        graphics.beginFill(0xf55142);
        app.stage.addChild(graphics);
        graphics.x = mycanvasWidth
        graphics.y = mycanvasHeight
        graphics.drawRect(0, -50, 500, this.state.progressValue)
        app.ticker.speed = 1
        app.ticker.add(() => {
            graphics.clear()
            graphics.beginFill(0xf55142);
            graphics.x = mycanvasWidth
            graphics.y = mycanvasHeight
            if (this.state.progressValue < -22100){
                var delay = function(s){
                    return new Promise(function(resolve, reject){
                        setTimeout(resolve,s); 
                    });
                };
                delay().then(() => {
                    app.ticker.stop()
                    return delay(800)
                }).then(() => {
                    this.setState({ checked: false })
                    this.setState({ checked2: false })
                    return delay(1000)
                }).then(() => {
                    this.setState({ isGameOver: true })
                    this.setState({ checked: true })
                    this.setState({ checked2: true })
                })
            }
            let nextNum = this.state.progressValue
            this.setState({ progressValue: nextNum - 0.55})
            console.log(this.state.progressValue)
            graphics.drawRect(0, -50, 500, nextNum / 36.83)
        })
    }
    componentDidMount(){
        var that = this;
        this.setupPixi()
		setTimeout(function() {
			that.show();
        }, that.props.wait);
    }
    componentWillUnmount() {
        clearInterval(this.timerID);
    }
	show() {
        var that = this
		var delay = function(s){
            return new Promise(function(resolve, reject){
                setTimeout(resolve,s); 
            });
        };
        delay().then(function(){
            that.setState({checked: true})
            that.setState({checked2: true})
        })
    }
    toggleAnswer = (percentage) => {
        let delay = (s) => {
            return new Promise((resolve, reject) => {
                setTimeout(resolve, s); 
            });
        };
        delay().then(() => {
            let nextNum = this.state.progressValue
            nextNum -= percentage * 1000
            this.setState({ progressValue: nextNum })
            return delay(800)
        }).then(() => {
            this.setState({ checked: false })
            return delay(800);
        }).then(() => {
            if (this.state.quizNum < 4){
                let nextProblem = this.state.quizNum + 1
                this.setState({ quizNum: nextProblem })
            }
        }).then(() => {
            this.setState({ checked: true })
        })
    }
    toggleFinalAnswer = (percentage) => {
        var delay = function(s){
            return new Promise(function(resolve, reject){
                setTimeout(resolve,s); 
            });
        };
        delay().then(() => {
            let nextNum = this.state.progressValue
            nextNum -= percentage * 1000
            console.log(nextNum)
            return nextNum
        }).then((nextNum) => {
            this.setState({ progressValue: nextNum })
            console.log(this.state.progressValue)
            return delay(1000)
        }).then(() => {
            this.state.app.ticker.stop()
        })
        if (this.state.progressValue < -22100){
            delay().then(() => {
                this.setState({ isGameOver: true })
                return delay(800)
            }).then(() => {
                this.setState({ checked: false })
                this.setState({ checked2: false })
                return delay(1000)
            }).then(() => {
                this.setState({ checked: true })
                this.setState({ checked2: true })
            })
        } else {
            delay().then(() => {
                return delay(800)
            }).then(() => {
                this.setState({ checked: false })
                this.setState({ checked2: false })
                return delay(1000)
            }).then(() => {
                this.setState({ isClear: true })
            }).then(() => {
                this.setState({ checked: true })
                this.setState({ checked2: true })
            })
        }
    }
    toggleAgain = () => {
        window.location.reload();
    }
    render(){
        if (this.state.isGameOver) {
            return(
                <Fade in={this.state.checked} timeout={800}>
                    <div className="mycanvas">
                        <h1 style={{marginTop: "25vh", marginBottom: "5vh"}}>GameOver</h1>
                        <button onClick={this.toggleAgain}>重新挑戰？</button>
                    </div>
                </Fade>
            )
        }
        else if (this.state.isClear && !this.state.isGameOver){
            return(
                <div>
                    <Fade in={this.state.checked} timeout={800}>
                        <div className="mycanvas">
                            <h1 style={{marginTop: "25vh", marginBottom: "5vh"}}>成功過關！</h1>
                            <button onClick={this.toggleAgain}>再玩一次？</button>
                        </div>
                    </Fade>
                </div>
            )
        }
        else if (this.state.quizNum < problems.length - 1){
            return(
                <div>
                    <Fade in={this.state.checked} timeout={800}>
                        <div className="problem">
                            <div className="textcontainer">
                                <div className="question">
                                    <h2>{problems[this.state.quizNum]['title']}</h2>
                                </div>
                            </div>
                            <div className="select">
                                <button onClick={this.toggleAnswer.bind(this, problems[this.state.quizNum]["options"][0]["percentage"])} id="button"><AcUnitIcon/></button>
                                <button onClick={this.toggleAnswer.bind(this, problems[this.state.quizNum]["options"][1]["percentage"])} id="button"><AddAPhotoIcon/></button>
                            </div>
                        </div>
                    </Fade>
                    <Fade in={this.state.checked2} timeout={800}>
                        <div>
                            <canvas id="progressbar"></canvas>
                        </div>
                    </Fade>
                </div>
            )
        }
        else if (this.state.quizNum === 4){
            return(
                <div>
                    <Fade in={this.state.checked} timeout={800}>
                        <div className="problem">
                            <div className="textcontainer">
                                <div className="question">
                                    <h2>{problems[this.state.quizNum]['title']}</h2>
                                </div>
                            </div>
                            <div className="select">
                                <button onClick={this.toggleFinalAnswer.bind(this, problems[this.state.quizNum]["options"][0]["percentage"])} id="finalbutton"><AcUnitIcon/></button>
                                <button onClick={this.toggleFinalAnswer.bind(this, problems[this.state.quizNum]["options"][1]["percentage"])} id="finalbutton"><AddAPhotoIcon/></button>
                                <button onClick={this.toggleFinalAnswer.bind(this, problems[this.state.quizNum]["options"][2]["percentage"])} id="finalbutton"><AcUnitIcon/></button>
                            </div>
                        </div>
                    </Fade>
                    <Fade in={this.state.checked2} timeout={800}>
                        <div>
                            <canvas id="progressbar"></canvas>
                        </div>
                    </Fade>
                </div>
            )
        }
    }
}